.Instructors_page {
  display: flex;
  justify-content: center;
}

.Instructors_page .container {
  width: 1170px;
  margin: 30px 0px;
  padding: 0 15px;
}

.Instructors_page .page {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-column-gap: 30px;
  grid-row-gap: 30px;
}
